import { useLocation, useNavigate, useParams } from "react-router-dom";
import style from "../../../AdmissionManager/OneOngoingAdmission/OneOngoingAdmission.module.css";
import { useCallback, useState } from "react";
import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import AddButtonWrapper from "../../../../Ui/Button/AddButtonWrapper";
import { useTranslation } from "react-i18next";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import HostelAdmissionApplicationReject from "./HostelAdmissionApplicationReject";
import HostelAdmissionApplicationFormUpdateReject from "./HostelAdmissionApplicationFormUpdateReject";
import CustomCreateButton from "../../../../Ui/Button/CustomCreateButton";
import HostelAdmissionApplicationFormUpdateRejectedReason from "./HostelAdmissionApplicationFormUpdateRejectedReason";
import HostelWhichActionStaff from "../HostelWhichActionStaff/HostelWhichActionStaff";
import HostelSelectFeeStructure from "../../FeeStructure/HostelSelectFeeStructure/HostelSelectFeeStructure";
import { useHostelOngoingApplicationSelect } from "../../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";
const HostelAdmissionApplicationCard = ({
  instituteId,
  applicant,
  hid,
  bid,
  cmid,
  appId,
  onRefetch,
  admissionIntake,
  unitId,
}) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();
  const getQuery = useLocation();

  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [hostelOngoingApplicationSelect] = useHostelOngoingApplicationSelect();

  const onViewApplicantProfile = useCallback(() => {
    navigate(`/q/${params.username}/student/profile`, {
      state: {
        sid: applicant?.student?._id,
        viewAs: "ADMISSION_ADMIN",
        instituteId: instituteId,
        // isExtraInfo:
        //   getQuery.state?.role === "ONGOING_VIEW_ACCESS"
        //     ? null
        //     : "ONGOING_APPLICATION",
        // isExtraData: {
        //   appId: appId,
        //   sid: applicant?.student?._id,
        //   hid: hid,
        //   bid: bid,
        //   cmid: cmid,
        //   reject_status: applicant?.reject_status,
        //   admissionIntake: admissionIntake,
        //   staffId: getQuery.state?.sid,
        // },
        whichUrl:
          getQuery.state?.urlOffSet === "Institute" ? "INSTITUTE" : null,
        onlyReadView: getQuery.state?.role,
        // subjectData: {
        //   subjectChange: "authorized",
        //   appId: appId,
        // },
      },
    });
  }, [
    navigate,
    params.username,
    instituteId,
    applicant?.student?._id,
    appId,
    hid,
    bid,
    cmid,
  ]);

  const onSelectStudent = (e) => {
    e.stopPropagation();
    setStep(1);
  };
  const onClose = () => {
    setStep("");
  };
  const onCancelStudent = (e) => {
    e.stopPropagation();
    setStep(2);
  };
  const onFormCancelStudent = (e) => {
    e.stopPropagation();
    setStep(3);
  };
  const onViewApplication = (e) => {
    e.stopPropagation();
  };
  const onRejectionReason = (e) => {
    e.stopPropagation();
    setStep(4);
  };

  const onOpenMenu = (e) => {
    e.stopPropagation();
    setStep(5);
  };
  const onEditForm = (e) => {
    e.stopPropagation();

    navigate(`/q/${params.username}/edit/student`, {
      state: {
        sid: applicant?.student?._id,
        openAs: "CLASS_TEACHER_SIDE",
        subTypeOpen: "ADMISSION_ADMIN",
        extraData: {
          regeneration_status: "Yes",
        },
      },
    });
  };

  const onSelectedStrucutre = (feeStructure, etData) => {
    if (applicant?.student?._id && appId && feeStructure?._id) {
      setDisabled((pre) => !pre);
      let obj = null;
      if (admissionIntake?.intake_visibility) {
        obj = {
          fee_struct: feeStructure?._id,
          staffId: getQuery.state?.sid,
          intake_type: etData,
        };
      } else {
        obj = {
          fee_struct: feeStructure?._id,
          staffId: getQuery.state?.sid,
        };
      }
      hostelOngoingApplicationSelect({
        sid: applicant?.student?._id,
        appId: appId,
        bodyParameter: obj,
      })
        .then(() => {
          onRefetch();
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };

  return (
    <>
      <div
        className={style.oac_container}
        onClick={onViewApplicantProfile}
        title="View Profile"
      >
        <div className={style.oac_container_inner}>
          <img
            alt="Student Request Avatar"
            src={
              applicant?.student?.studentProfilePhoto
                ? `${imageShowUrl}/${applicant?.student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
          />
          <div className={style.oac_container_inner_info}>
            <h6>{`${applicant?.student?.studentFirstName ?? ""} ${
              applicant?.student?.studentMiddleName
                ? applicant?.student?.studentMiddleName
                : ""
            } ${applicant?.student?.studentLastName ?? ""}`}</h6>
            <p>
              {t("form_no_only")}: {applicant?.student?.form_no ?? ""}
            </p>
            <p>
              {t("applied_on")}:{" "}
              {dayjs(applicant?.apply_on).format("DD MMM YYYY")}
            </p>
          </div>
        </div>
        <div className={style.oac_container_contact}>
          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/gender.svg`}
              alt="email icon"
            />
            <p>{applicant?.student?.studentGender}</p>
          </div>

          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/phone.svg`}
              alt="phone icon"
            />
            <p>
              {applicant?.student?.user?.userPhoneNumber
                ? applicant?.student?.user?.userPhoneNumber
                : applicant?.student?.user?.userEmail
                ? applicant?.student?.user?.userEmail
                : applicant?.student?.studentPhoneNumber
                ? applicant?.student?.studentPhoneNumber
                : applicant?.student?.studentParentsPhoneNumber
                ? applicant?.student?.studentParentsPhoneNumber
                : applicant?.student?.studentEmail ?? ""}
            </p>
          </div>
        </div>

        <div className={style.oac_container_stuff}>
          <a
            href={`${pdfCdnUrl}/${applicant?.student?.application_print?.[0]?.value}`}
            target="_blank"
            rel="noopener noreferrer"
            style={{
              textDecoration: "none",
              marginTop: "-3px",
            }}
            onClick={onViewApplication}
          >
            <img
              src={`${assestsUserAdmissionUrl}/download.svg`}
              alt="receipt icon"
              title="View Application Form"
            />
          </a>
          {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
            <>
              <AddButtonWrapper>
                {applicant?.reject_status ? (
                  <CustomCreateButton
                    label="rejection_reason"
                    onAction={onRejectionReason}
                    customStyle={{
                      padding: "0.4rem 0.7rem",
                      fontSize: "13px",
                      "--ccb-bg": "#e6686830",
                      "--ccb-b": "#ed4a4a",
                      "--ccb-text": "#f00b0b",
                      "--ccb-bg-h": "#eb3232",
                      "--ccb-text-h": "#ffffff",
                      minWidth: "max-content",
                      justifyContent: "center",
                    }}
                  />
                ) : (
                  <>
                    <AdmissionButton
                      label="select"
                      onAction={onSelectStudent}
                      customStyle={{
                        padding: "0.4rem 0.7rem",
                        fontSize: "13px",
                      }}
                    />
                    <AdmissionButton
                      label="reject"
                      onAction={onCancelStudent}
                      customStyle={{
                        padding: "0.4rem 0.7rem",
                        borderColor: "rgb(176 174 174)",
                        fontSize: "13px",
                        opacity: "0.9",
                      }}
                    />
                    <AdmissionButton
                      label="edit_from"
                      onAction={onEditForm}
                      customStyle={{
                        padding: "0.4rem 0.7rem",
                        borderColor: "rgb(176 174 174)",
                        fontSize: "13px",
                        opacity: "0.9",
                      }}
                    />
                    <AdmissionButton
                      label="reject_insufficient"
                      onAction={onFormCancelStudent}
                      customStyle={{
                        padding: "0.4rem 0.7rem",
                        borderColor: "rgb(176 174 174)",
                        fontSize: "13px",
                        opacity: "0.9",
                        minWidth: "max-content",
                      }}
                    />
                  </>
                )}
              </AddButtonWrapper>
            </>
          )}
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onOpenMenu}
          />
        </div>
      </div>

      {step === 1 ? (
        <HostelSelectFeeStructure
          title="select_fee_structre_of_student"
          onClose={onClose}
          onSelectedStrucutre={onSelectedStrucutre}
          hid={hid}
          bid={bid}
          cmid={cmid}
          isAdmissionIntake={admissionIntake?.intake_visibility}
          isOngoing
          unitId={unitId}
        />
      ) : step === 2 ? (
        <HostelAdmissionApplicationReject
          onClose={onClose}
          appId={appId}
          sid={applicant?.student?._id}
          onRefetch={onRefetch}
        />
      ) : step === 3 ? (
        <HostelAdmissionApplicationFormUpdateReject
          onClose={onClose}
          appId={appId}
          sid={applicant?.student?._id}
          onRefetch={onRefetch}
        />
      ) : step === 4 ? (
        <HostelAdmissionApplicationFormUpdateRejectedReason
          onClose={onClose}
          reason={applicant?.reject_status}
        />
      ) : step === 5 ? (
        <HostelWhichActionStaff
          onClose={onClose}
          studentId={applicant?.student?._id}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default HostelAdmissionApplicationCard;
