import { useState } from "react";
import HostelAdmissionReviewConfirm from "./HostelAdmissionReviewConfirm";
import HostelSelectRoom from "./HostelSelectRoom";

const HostelAdmissionReviewConfig = ({
  appId,
  applicant,
  onClose,
  onRefetch,
  isMultiple,
  instituteId,
  unitId,
}) => {
  const [step, setStep] = useState(1);
  const [state, setState] = useState({
    hrid: "",
  });
  const onSelectRoom = (val) => {
    setState((prev) => ({
      ...prev,
      hrid: val,
    }));
    setStep(2);
  };
  return step === 1 ? (
    <HostelSelectRoom
      onClose={onClose}
      huid={unitId}
      onSelect={onSelectRoom}
      selectedRoom={state?.hrid?._id}
    />
  ) : step === 2 ? (
    <HostelAdmissionReviewConfirm
      appId={appId}
      onClose={() => setStep(1)}
      applicant={applicant}
      onRefetch={onRefetch}
      isMultiple={isMultiple}
      onParentClose={onClose}
      instituteId={instituteId}
      unitId={unitId}
      state={state}
    />
  ) : null;
};

export default HostelAdmissionReviewConfig;
