import { useEffect, useState } from "react";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../PlateformUser/PlateformUser.module.css";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import HostelSelectRoomCard from "./HostelSelectRoomCard";
import { useTranslation } from "react-i18next";
import { useHostelAllRoomList } from "../../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";
const HostelSelectRoom = ({ onClose, huid, onSelect, selectedRoom }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [list, setList] = useState([]);
  const { hostelAllRoomList, hostelAllRoomListLoading } = useHostelAllRoomList({
    data: {
      huid: huid,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !huid,
  });

  useEffect(() => {
    if (hostelAllRoomList?.list?.length > 0) {
      setList(() =>
        hostelAllRoomList?.list?.filter((val) =>
          val?.vacant_count > 0 ? val : null
        )
      );
    }
  }, [hostelAllRoomList?.list?.length]);

  const onSearchEvent = (e) => {
    if (e.target.value) {
      setSearch(e.target.value);
      let rectifyData = getFilter(hostelAllRoomList?.list, e.target.value);
      setList(rectifyData);
    } else {
      setSearch("");
      setList(hostelAllRoomList?.list);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("select_unit_room")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0.3rem",
            }}
          >
            <div className={style.assign_search_container}>
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>
            {list?.map((room) => (
              <HostelSelectRoomCard
                room={room}
                onSelect={onSelect}
                selectedRoom={selectedRoom}
              />
            ))}

            {hostelAllRoomListLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : search ? (
              list?.length === 0 && (
                <EmptyMessage
                  title="No rooms found related this search."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            ) : (
              list?.length === 0 &&
              !hostelAllRoomListLoading && (
                <EmptyMessage
                  title="No rooms."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default HostelSelectRoom;

const getFilter = (list, search) => {
  let data = list?.filter((bat) => {
    if (bat?.room_name?.toLowerCase()?.includes(search?.toLowerCase()))
      return bat;
    else return null;
  });
  return data;
};
