import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../AdmissionManager/OngoingAdmission/OngoingAdmission.module.css";
const HostelSelectRoomCard = ({ room, onSelect, selectedRoom }) => {
  const onClick = () => {
    onSelect(room);
  };
  return (
    <div
      className={
        selectedRoom === room?._id
          ? `${style.admission_confirm_class_card} ${style.admission_confirm_class_card_active}`
          : style.admission_confirm_class_card
      }
      onClick={onClick}
    >
      <img src={`${assestsUserFinanceUrl}/class.svg`} alt="batch Avatar" />
      <div
        className={style.admission_collect_docs}
        style={{
          marginTop: "0",
        }}
      >
        <h6>
          {room?.room_name ?? ""} {" , Vacant: "}
          {room?.vacant_count ?? 0}
        </h6>
      </div>
    </div>
  );
};

export default HostelSelectRoomCard;
