import { useLocation, useSearchParams } from "react-router-dom";
import { useAdmissionOneOngoingApplication } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import HostelAdmissionApplication from "./HostelAdmissionApplication/HostelAdmissionApplication";
import HostelDocumentCollection from "./HostelDocumentCollection/HostelDocumentCollection";
import HostelFeesCollection from "./HostelFeesCollection/HostelFeesCollection";
import HostelAdmissionConfirm from "./HostelAdmissionConfirm/HostelAdmissionConfirm";
import HostelAdmissionMultipleConfirm from "./HostelAdmissionConfirm/HostelAdmissionMultipleConfirm";
import HostelAdmissionReview from "./HostelAdmissionReview/HostelAdmissionReview";
import HostelAdmissionMultipleReview from "./HostelAdmissionReview/HostelAdmissionMultipleReview";
import HostelAdmissionAllottment from "./HostelAdmissionAllottment/HostelAdmissionAllottment";
// import HostelAdmissionCancelled from "./HostelAdmissionCancelled/HostelAdmissionCancelled";

const OneHostelOngoingAdmissionConfig = ({ instituteId, hid, financeId }) => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const { admissionOneOngoingApplication } = useAdmissionOneOngoingApplication({
    appId: getQuery.state?.appId,
    skip: !getQuery.state?.appId,
  });
  // console.info(
  //   "admissionOneOngoingApplication",
  //   admissionOneOngoingApplication
  // );
  return (
    <>
      {!searchParams.get("n") ? (
        <HostelAdmissionApplication
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          hid={hid}
        />
      ) : searchParams.get("n") === "document" ? (
        <HostelDocumentCollection
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          hid={hid}
        />
      ) : searchParams.get("n") === "fees" ? (
        <HostelFeesCollection
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          hid={hid}
          financeId={financeId}
        />
      ) : searchParams.get("n") === "confirmed" ? (
        <HostelAdmissionConfirm
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
        />
      ) : searchParams.get("n") === "confirmed-multi" ? (
        <HostelAdmissionMultipleConfirm
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
        />
      ) : searchParams.get("n") === "review" ? (
        <HostelAdmissionReview
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          hid={hid}
        />
      ) : searchParams.get("n") === "review-multi" ? (
        <HostelAdmissionMultipleReview
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          hid={hid}
        />
      ) : searchParams.get("n") === "allottment" ? (
        <HostelAdmissionAllottment
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
        />
      ) : //  searchParams.get("n") === "cancelled" ? (
      //   <HostelAdmissionCancelled
      //     applicationDetail={admissionOneOngoingApplication}
      //     instituteId={instituteId}
      //   />
      // ) :
      null}
    </>
  );
};

export default OneHostelOngoingAdmissionConfig;
