import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../PlateformUser/PlateformUser.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useTranslation } from "react-i18next";
import styleAdmission from "../../../AdmissionManager/OneOngoingAdmission/OneOngoingAdmission.module.css";
import { useLocation } from "react-router-dom";
// import CustomSelectRoomDropdown from "./CustomSelectRoomDropdown";
import { useHostelOngoingApplicationAllotBed } from "../../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";
// import {
//   existingRequiredField,
//   requiredField,
// } from "../../../../Validation/functions/checkingRequiredField";
const HostelAdmissionReviewConfirm = ({
  appId,
  onClose,
  onParentClose,
  applicant,
  onRefetch,
  isMultiple,
  instituteId,
  unitId,
  state,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();

  const [disabled, setDisabled] = useState(false);
  const [hostelOngoingApplicationAllotBed] =
    useHostelOngoingApplicationAllotBed();

  const onAllotment = () => {
    // let validation = requiredField(state, []);
    // if (existingRequiredField(validation)) {
    if (appId && state?.hrid?._id && applicant && instituteId && unitId) {
      setDisabled((pre) => !pre);
      hostelOngoingApplicationAllotBed({
        appId: appId,
        bodyParameter: {
          dataList: isMultiple ? applicant : [applicant?.student?._id],
          staffId: getQuery.state?.sid,
          huid: unitId,
          hrid: state?.hrid?._id,
          id: instituteId,
        },
      })
        .then(() => {
          onRefetch();
          onParentClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
    // } else {
    //   setErrorField(validation);
    // }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("allot_bed_to_hostelite")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0.3rem",
            }}
          >
            <div className={styleAdmission.admission_class_confirm}>
              {/* <CustomSelectRoomDropdown
                huid={unitId}
                onSelect={onSelectRoom}
                errorShow={errorField["hrid"]}
                viewAs="NORMAL_FILTER"
                defalutValue={
                  state?.hrid?._id
                    ? `${state?.hrid?.room_name} , Vacant: ${state?.hrid?.vacant_count}`
                    : ""
                }
                customStyleContainer={{
                  marginBottom: "1rem",
                }}
              /> */}
              {isMultiple ? (
                <>
                  {t("alloting")}{" "}
                  <span
                    style={{
                      fontWeight: "600",
                    }}
                  >
                    {applicant?.length}
                  </span>{" "}
                  {t("students")} {` `}
                </>
              ) : (
                <>
                  {t("lloting_student")}{" "}
                  <span
                    style={{
                      fontWeight: "600",
                    }}
                  >
                    {` ${applicant?.student?.studentFirstName ?? ""} ${
                      applicant?.student?.studentMiddleName
                        ? applicant?.student?.studentMiddleName
                        : ""
                    } ${applicant?.student?.studentLastName ?? ""} `}
                  </span>
                </>
              )}
              ,{t("to")}{" "}
              {state?.hrid?.room_name ? (
                <span
                  style={{
                    fontWeight: "600",
                  }}
                >{` ${state?.hrid?.room_name ?? ""} , Vacant: ${
                  state?.hrid?.vacant_count ?? ""
                }`}</span>
              ) : null}
            </div>
            <CreateButton label="confirm" onAction={onAllotment} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default HostelAdmissionReviewConfirm;
